<template>
  <div class="p-py-3 p-px-3" v-if="$store.state.breadcrumb.length > 0">
    <Breadcrumb :home="home" :model="$store.state.breadcrumb" />
  </div>
  <div class="p-grid p-nogutter p-px-3">
    <div class="p-col-12 p-lg-6 p-lg-offset-3 p-xl-4 p-xl-offset-4 p-col-nogutter">
      <form @submit.stop.prevent="onSave" >
        <Card>
          <template #title>
            {{ $t('account.profile') }}
          </template>
          <template #content>
            <div class="p-fluid">
              <div class="p-field">
                <label>{{ $t('account.first_name') }}</label>
                <InputText type="text"
                           :placeholder="$t('account.first_name')"
                           v-model.trim="v$.form.first_name.$model"
                           :class="{'p-invalid': v$.form.first_name.$dirty && v$.form.first_name.$invalid}"
                           :disabled="$store.state.loading"/>

                <ValidationErrors :form="v$.form" attribute="first_name" />
              </div>
              <div class="p-field">
                <label>{{ $t('account.last_name') }}</label>
                <InputText type="text"
                           :placeholder="$t('account.last_name')"
                           v-model.trim="v$.form.last_name.$model"
                           :class="{'p-invalid': v$.form.last_name.$dirty && v$.form.last_name.$invalid}"
                           :disabled="$store.state.loading"/>

                <ValidationErrors :form="v$.form" attribute="last_name" />
              </div>
            </div>
          </template>
          <template #footer>
            <Button icon="pi pi-check" :label="$t('save')" type="submit"
                    :loading="$store.state.loading"
                    :disabled="$store.state.loading"/>
          </template>
        </Card>
      </form>
    </div>
  </div>
</template>

<script>
import {defineComponent} from "vue";
import {required} from '@vuelidate/validators'
import useVuelidate from "@vuelidate/core";
import ValidationErrors from "@/components/ValidationErrors";
import { ACCOUNT_UPDATE_PROFILE } from "../../store/actions/account";

export default defineComponent({
  components: {ValidationErrors},
  setup() {
    return {
      v$: useVuelidate()
    }
  },
  data() {
    return {
      home: {icon: 'pi pi-home', to: '/'},
      form: {
        first_name: '',
        last_name: ''
      }
    }
  },
  mounted() {
    Object.keys(this.form).forEach(key => {
      this.form[key] = this.$store.state.account.profile[key];
    })
    this.$breadcrump(this.$t('account.profile'));
  },
  methods: {
    async onSave() {
      this.v$.form.$touch()
      if (this.v$.form.$error) {
        return;
      }

      let resp = await this.$api.account.profile.post(this.form)
      if (resp.success) {
        this.$store.commit(ACCOUNT_UPDATE_PROFILE, this.form);

        this.$toast.add({
          severity: 'success',
          summary: this.$t('account.profile'),
          detail: this.$t('successfully-saved'),
          life: 3000
        });
      }
    }
  },
  validations() {
    return {
      form: this.$setMessages({
        first_name: {
          required
        },
        last_name: {
          required
        }
      })
    }
  }
})
</script>

